<script>
    import { onMount } from 'svelte';
    import CodeSource from '../components/CodeSource.svelte'; 
    import TableauParams from '../components/TableauParams.svelte'; 

    let mounted = false;
    let tableauParametres = [];

    onMount(() => {
        tableauParametres = obtenirTableauParametres();
        message1();
        message2();
        message3();
        message4();
        message5();
        message6();
        mounted = true;
    });

    function obtenirTableauParametres() {
        return [
            {nom: "type", type: "String (Optionnel)", description: `Valeurs possibles : <span class="utd-emphase-gris">information</span>, <span class="utd-emphase-gris">avertissement</span>, <span class="utd-emphase-gris">succes</span>, <span class="utd-emphase-gris">erreur</span>.`},
            {nom: "titre", type: "String", description: `Titre du message.`},
            {nom: "corps", type: "String", description: `Corps du message.`},
            {nom: "texteBoutonPrimaire", type: "String", description: `Texte du bouton primaire. Si non spcifi, le bouton primaire n'est pas affich.`},
            {nom: "texteBoutonSecondaire", type: "String (Optionnel)", description: `Texte du bouton secondaire. Si non spcifi, le bouton secondaire n'est pas affich.`},
            {nom: "texteBoutonFermer", type: "String (Optionnel)", description: `Texte du bouton de fermeture du message. Dfaut "Fermer" ou "Close" en anglais.`},
            {nom: "estBoutonsTexteLong", type: "Boolean (Optionnel)", description: `Indique si le texte des boutons est long. Si c'est le cas, les boutons sont affichs verticalement en pleine largeur si la largeur d'cran est infrieure ou gale  525px. (Au lieu de 425px normalement)`},
            {nom: "idControleFocusFermeture", type: "String (Optionnel)", description: `Id du contrle auquel on redonne le focus  la fermeture de la fentre de message. Si non spcifi, focus redonn  l'lment actif avant l'affichage du message.`},                        
        ];
    }

    function message1() {
        document.getElementById('btnTest1').addEventListener('click', () => {
        const params =             
            {type: "avertissement",
             titre: "Continuum espace temps en pril",
             corps: "<p>Vos dernires actions ont provoqu de graves perturbations du continuum espace temps.</p><p>Dsirez-vous poursuivre avec la destruction potentielle de l'univers ou corriger le tout?</p>",
             texteBoutonPrimaire: "Corriger",
             texteBoutonSecondaire: "Dtruire"};

        utd.message.afficher(params)
            .then((retour) => {
                document.getElementById('resultat1').innerText = `Raison fermeture = ${retour}`;
            });
        });
    }

    function message2() {
        document.getElementById('btnTest2').addEventListener('click', () => {
        const params =             
            {type: "information",
             titre: "Continuum espace temps en pril",
             corps: "<p>Vos dernires actions ont provoqu de graves perturbations du continuum espace temps.</p><p>Esprons que tout ira pour le mieux.</p>",
             texteBoutonPrimaire: "Je m'excuse"
            };

        utd.message.afficher(params)
            .then((retour) => {
                document.getElementById('resultat2').innerText = `Raison fermeture = ${retour}`;
            });
        });
    }

    function message3() {
        document.getElementById('btnTest3').addEventListener('click', () => {
        const params =             
            {type: "succes",
             titre: "Continuum espace temps sauv",
             corps: "<p>Vos dernires actions ont rpar les graves perturbations du continuum espace temps.</p><p>Vous avez sauv le monde!</p>",
             texteBoutonPrimaire: "Merci!"
            };

        utd.message.afficher(params)
            .then((retour) => {
                document.getElementById('resultat3').innerText = `Raison fermeture = ${retour}`;
            });
        });
    }

    function message4() {
        document.getElementById('btnTest4').addEventListener('click', () => {
        const params = {
            type: "erreur",
            titre: "Continuum espace temps bris",
            corps: "<p>Une erreur est survenue lors de vos tentatives de rparation des perturbations du continuum espace temps.</p><p>Il n'y a rien  faire.</p>",
            texteBoutonPrimaire: "D'accord"
        };

        utd.message.afficher(params)
            .then((retour) => {
                document.getElementById('resultat4').innerText = `Raison fermeture = ${retour}`;
            });
        });
    }

    function message5() {
        document.getElementById('btnTest5').addEventListener('click', () => {
        const params =             
            {titre: "Continuum espace temps en pril",
             corps: "<p>Vos dernires actions ont provoqu de graves perturbations du continuum espace temps.</p><p>Dsirez-vous poursuivre avec la destruction potentielle de l'univers ou corriger le tout?</p>",
             texteBoutonPrimaire: "Corriger",
             texteBoutonSecondaire: "Dtruire"};

        utd.message.afficher(params)
            .then((retour) => {
                document.getElementById('resultat5').innerText = `Raison fermeture = ${retour}`;
            });
        });
    }

    function message6() {    
        document.getElementById('btnTest6').addEventListener('click', () => {
            const params =             
                {titre: "Continuum espace temps en pril",
                corps: "<p>Vos dernires actions ont provoqu de graves perturbations du continuum espace temps.</p><p>Dsirez-vous poursuivre avec la destruction potentielle de l'univers ou corriger le tout?</p>",
                texteBoutonPrimaire: "Corriger et demander pardon",
                texteBoutonSecondaire: "Dtruire",
                estBoutonsTexteLong: true
                };

            utd.message.afficher(params)
                .then((retour) => {
                    document.getElementById('resultat6').innerText = `Raison fermeture = ${retour}`;
                });
        });
    }
</script>
<div class="documentation">
    <h1>Message</h1>

    <utd-menu-ancres selecteur="#main h2">
    </utd-menu-ancres>

    <h2>Description</h2>
    <p>Il s'agit du composant <strong>dialogue dalerte</strong> du systme de design Quebec.ca.</p>
    <p>Il apparat lorsquune action immdiate est requise. Le dialogue dalerte contient un message ainsi quun ou plusieurs boutons daction. </p>

    <h3>Rfrence systme de design Quebec.ca</h3>
    <a href="https://design.quebec.ca/composantes/action/dialog-modal" target="_blank">Voir les spcifications sur le site de design Quebec.ca</a>

    <h3>Particularits vs. le systme de design Quebec.ca</h3>
    <p>Lorsqu'un type est spcifi, nous ajoutons une icne au titre du message et nous retirons le filet de couleur saumon sous le titre du message.</p>
    <p>Nous estimons que cela ajoute un lment visuel fort qui indique rapidement de quel type de message il s'agit.</p>

    <h2>Mthodes</h2>
    <h3>utd.message.afficher</h3>
    <h4>Paramtres</h4>
    <TableauParams parametres="{tableauParametres}">
    </TableauParams>

    <h4>Retour</h4>
    <p>Une promesse javascript dont le rsultat contient une chane de caractres contenant le raison de fermeture du message. Les valeurs possibles sont : </p>
    <ul class="utd">
        <li><span class="utd-emphase-gris">primaire</span>, si le bouton primaire a t cliqu.</li>
        <li><span class="utd-emphase-gris">secondaire</span>, si le bouton secondaire a t cliqu.</li>
        <li><span class="utd-emphase-gris">clickBackdrop</span>, si l'utilisateur a cliqu dans le backdrop afin de fermer le message.</li>
        <li><span class="utd-emphase-gris">boutonFermer</span>, si l'utilisateur a cliqu sur le bouton "Fermer (X)" afin de fermer le message.</li>
        <li><span class="utd-emphase-gris">escape</span>, si l'utilisateur appuy sur la touche "ESC" afin de fermer le message.</li>
    </ul>

    <h2>Accessibilit</h2>
    <p>Les <a href="https://www.w3.org/WAI/ARIA/apg/example-index/dialog-modal/dialog">rgles du W3C relatives aux dialogues de type "messages"</a> sont suivies  la lettre.</p>
    <p>Il semble que l'implmentation dans NVDA provoque la lecture du titre de la modale au lecteur cran deux fois (une au dbut et une  la fin). Le libell du bouton qui reoit le focus est galement lu deux fois.</p>
    <p>Il serait possible de donner le focus au texte du message, mais dans ce cas c'est un bout du texte du message qui serait lu deux fois. Dans tous les cas le titre de la modale est lu deux fois.</p>
    <p>On prfre donc donner le focus au bouton mme si son libell est lu deux fois. Il s'agit malgr tout de la meilleure solution possible, et il semble qu'il n'y a malheureusement rien  faire.</p>
</div>

<h2>Exemples</h2>
<h3>1- Message de type avertissement avec 2 boutons</h3>
<button type="button" id="btnTest1" class="utd-btn secondaire mb-16">Test 1</button>
<div id="resultat1"></div>
{#if mounted}
    <CodeSource codeSource="{message1.toString()}" language="language-javascript">
    </CodeSource>   
{/if}   


<h3>2- Message de type information avec 1 bouton</h3>
<button type="button" id="btnTest2" class="utd-btn secondaire mb-16">Test 2</button>
<div id="resultat2"></div>
{#if mounted}
    <CodeSource codeSource="{message2.toString()}" language="language-javascript">
    </CodeSource>   
{/if}   


<h3>3- Message de type succs avec 1 bouton</h3>
<button type="button" id="btnTest3" class="utd-btn secondaire mb-16">Test 3</button>
<div id="resultat3"></div>
{#if mounted}
    <CodeSource codeSource="{message3.toString()}" language="language-javascript">
    </CodeSource>   
{/if}   


<h3>4- Message de type erreur avec 1 bouton</h3>
<button type="button" id="btnTest4" class="utd-btn secondaire mb-16">Test 4</button>
<div id="resultat4"></div>
{#if mounted}
    <CodeSource codeSource="{message4.toString()}" language="language-javascript">
    </CodeSource>   
{/if}   


<h3>5- Message sans type avec 2 boutons</h3>
<button type="button" id="btnTest5" class="utd-btn secondaire mb-16">Test 5</button>
<div id="resultat5"></div>
{#if mounted}
    <CodeSource codeSource="{message5.toString()}" language="language-javascript">
    </CodeSource>   
{/if}   


<h3>6- Message sans type avec 2 boutons et texte long</h3>
<button type="button" id="btnTest6" class="utd-btn secondaire mb-16">Test 6</button>
<div id="resultat6"></div>
{#if mounted}
    <CodeSource codeSource="{message6.toString()}" language="language-javascript">
    </CodeSource>   
{/if}   


