<script>
    import CodeSource from '../components/CodeSource.svelte'; 
    import { onMount } from 'svelte';
    import TableauParams from '../components/TableauParams.svelte'; 
    import TableauSlots from '../components/TableauSlots.svelte'; 


    let tableauParametres = [];
    let tableauSlots = [];

    onMount(() => {
        tableauParametres = obtenirTableauParametres();
        tableauSlots = obtenirTableauSlots();
        ajouterCodeExemple4();
    })

    function obtenirTableauParametres() {
        return [           
            {nom: "recherchable", type: "Boolen (Optionnel)", description: `Indique s'il est possible d'effectuer une recherche dans les valeurs possibles de la liste. (Dfaut : "false")`},
            {nom: "largeur", type: "String (Optionnel)", description: `Largeur de la liste. Valeurs possibles : <span class="utd-emphase-gris">lg</span> (528px), <span class="utd-emphase-gris">md</span> (342px), <span class="utd-emphase-gris">sm</span> (156px). (Dfaut : "md")`},
            {nom: "recherche-floue", type: "Boolen (Optionnel)", description: `Indique si la recherche doit tre prcise ou non, i.e. doit contenir exactement le terme recherch. Si la recherche est floue, un certain niveau d'erreur est permis (ex. une faute de frappe). (Dfaut : "true")`}
        ]
    }

    function obtenirTableauSlots() {
        return [
            {nom: "dfaut", description: `<p>Slot par dfaut. Aucun nom  fournir.</p><p>Contenu html entre les balises du contrle. Ne devrait tre que le contrle "select" original.</p>`}
        ]
    }

    function ajouterCodeExemple4() {   
        const controleUtd = document.getElementById('champUtd4')     
        document.getElementById('select4').addEventListener('change', (event) => {
            controleUtd.setAttribute('invalide', event.target.value === '' ? 'true': 'false')
        })
    }


</script>

<style type="text/css">
</style>

<div class="documentation">
    <h1>Liste droulante</h1>

    <utd-menu-ancres selecteur="#main h2">
    </utd-menu-ancres>

    <h2>Description</h2>
    <p>La <strong>liste droulante</strong> permet  l'utilisateur de slectionner une ou plusieurs des options contenues dans une liste.</p>
    <p>Il existe 3 types de liste droulante.</p>
    <p>La <strong>liste droulante standard</strong> est utilise lorsqu'un seul choix est requis, par exemple pour slectionner la province de rsidence dans une adresse postale.&nbsp;</p>
    <p>La <strong>liste droulante  slection multiple</strong> permet de choisir un ou plusieurs lments de la liste, comme la composante&nbsp;<a href="/composantes/formulaire/case-a-cocher">Case  cocher</a>. Elle ne devrait tre utilise uniquement lorsque les cases  cocher occupent trop d'espace dans l'cran.</p>
    <p>Cette liste est utilise pour permettre  l'utilisateur de slectionner des critres comme dans le cas de conditions de travail d'un emploi.</p>
    <p>La <strong>bote combine</strong> est une liste  slection unique ou multiple qui comprend un outil de recherche. Par dfaut la recherche permet un certain flou (Voir paramtres "recherche-floue" et "precision-recherche").</p>
    <p>Cette liste est utilise pour faciliter la slection d'lments lorsque la liste est trs longue, par exemple une liste de ministres et organismes.</p>

    <h3>Rfrence systme de design Qubec.ca</h3>
    <a href="https://design.Qubec.ca/composantes/formulaire/liste-deroulante" target="_blank">Voir les spcifications sur le site de design Qubec.ca</a>

    <h3>Particularits vs. le systme de design Qubec.ca</h3>
    <p>Un filet gris ple a t ajout sous le placeholder lorsqu'une liste  slection simple sans recherche est ouverte afin de mieux distinguer les choix possibles de l'lment slectionn.</p>

    <h3>Particularits techniques</h3>
    <p>Ce composant produit une liste droulante  partir d'une liste native (select). Il suffit d'intgrer  l'intrieur du prsent composant une liste droulante native avec les options que vous souhaitez et l'attribut "multiple" pour une liste  slection multiple.</p>
    <p>Les attributs du composant vous permettent de spcifier si la liste est recherchable (floue ou prcise), ainsi que sa largeur. La largeur devrait tre dtermine en fonction de la longueur des textes des choix possibles.</p>

    <h2>Attributs disponibles</h2>
    <TableauParams parametres="{tableauParametres}">
    </TableauParams>

    <h2>Slots disponibles</h2>
    <TableauSlots parametres="{tableauSlots}">
    </TableauSlots>
</div>
