<script>
    import CodeSource from '../components/CodeSource.svelte'; 
    import { onMount } from 'svelte';
    import TableauParams from '../components/TableauParams.svelte'; 
    import TableauSlots from '../components/TableauSlots.svelte'; 

    let tableauParametres = [];
    let tableauSlots = [];

    onMount(() => {
        tableauParametres = obtenirTableauParametres();
        tableauSlots = obtenirTableauSlots();
    })

    function obtenirTableauParametres() {
        return [
            {nom: "titre", type: "String (Optionnel)", description: `Titre affich dans la feuille associe  l'infobulle.`},
            {nom: "sr-titre", type: "String (Optionnel)", description: `Texte lecteur cran seulement en prfixe au titre affich dans la feuille associe  l'infobulle. Dfaut : "Aide concernant " et "Help about ".`},
            {nom: "sr-prefixe-contenu", type: "String (Optionnel)", description: `Texte lecteur cran seulement en prfixe au contenu affich dans la feuille associe  l'infobulle. Il permet d'amliorer l'accessibilit du composant en vitant de lire tout le contenu de l'infobulle lors de son ouverture.  Dfaut : "Dtails&nbsp;: " et "Details: ".`},
            {nom: "sr-bouton-ouvrir", type: "String (Optionnel)", description: `Texte lecteur cran seulement du bouton permettant d'ouvrir l'infobulle.`},
            {nom: "sr-bouton-fermer", type: "String (Optionnel)", description: `Texte lecteur cran seulement du bouton permettant de fermer l'infobulle.`},
            {nom: "contenu", type: "String (Optionnel)", description: `Texte  afficher dans la zone de contenu de l'infobulle.`}
        ];
    }

    function obtenirTableauSlots() {
        return [
            {nom: "contenu", description: `Texte  afficher dans la zone de contenu de l'infobulle.`},
            {nom: "texte-lie", description: `Texte li  l'infobulle (affich en bleu ple juste avant l'infobulle.`}
        ];
    }

</script>
<div class="documentation">
    <h1>Infobulle</h1>

    <utd-menu-ancres selecteur="#main h2">
    </utd-menu-ancres>

    <h2>Description</h2>
    <p>L<strong>infobulle</strong> est utilise pour fournir des informations contextuelles lies  un lment particulier.</p>

    <h3>Rfrence systme de design Quebec.ca</h3>
    <a href="https://design.quebec.ca/composantes/affichage-de-contenu/infobulle" target="_blank">Voir les spcifications sur le site de design Quebec.ca</a>

    <h3>Particularits vs. le systme de design Quebec.ca</h3>
    <p>Notre composant prend toujours l'aspect feuille, i.e. l'infobulle est toujours affiche au bas de la page et jamais  ct du bouton qui lui est associ.</p>

    <h2>Attributs disponibles</h2>
    <TableauParams parametres="{tableauParametres}">
    </TableauParams>

    <h2>Slots disponibles</h2>
    <TableauSlots parametres="{tableauSlots}">
    </TableauSlots>
</div>

