<script>
  import { onMount } from 'svelte';
  import CodeSource from '../components/CodeSource.svelte'; 
  import TableauParams from '../components/TableauParams.svelte'; 
  import TableauSlots from '../components/TableauSlots.svelte'; 

  let mounted = false;
  let tableauParametres = [];
  let tableauSlots = [];
  let afficherGrosContenu = false;

  onMount(() => {
    tableauParametres = obtenirTableauParametres();
    tableauSlots = obtenirTableauSlots();

    ajouterCodeDialogue1()
    ajouterCodeDialogue2()
    ajouterCodeDialogue3()
    ajouterCodeDialogue4()
    mounted = true
})

function obtenirTableauParametres() {
    return [
        {nom: "afficher", type: "Boolean (Optionnel)", description: `Permet de spcifier si la fentre modale doit tre affiche ou non.`},
        {nom: "type", type: "String (Optionnel)", description: `Type de la fentre modale (icne). Valeurs possibles : <span class="utd-emphase-gris">information</span>, <span class="utd-emphase-gris">avertissement</span>, <span class="utd-emphase-gris">succes</span>, <span class="utd-emphase-gris">erreur</span>.`},
        {nom: "titre", type: "String (Optionnel)", description: `Titre de la fentre modale.`},
        {nom: "sr-bouton-fermer", type: "String (Optionnel)", description: `Texte lecteur cran seulement du bouton permettant de fermer la fentre modale.`},
        {nom: "id-focus-ouverture", type: "String (Optionnel)", description: `Id du contrle auquel on veut forcer le focus une fois la fentre modale affiche si le comportement pas dfaut ne convient pas.`},
        {nom: "id-focus-fermeture", type: "String (Optionnel)", description: `Id du contrle auquel on veut forcer le focus  la fermeture de la fentre modale si le comportement par dfaut ne convient pas. Par dfaut le contrle redonne le focus  l'lment qui  ouvert la fentre modale (ex. un bouton).`},
        {nom: "boutons-texte-long", type: "Boolean (Optionnel)", description: `Affiche les boutons  partir de 525px et moins sous forme de colonne (au lieu du 425px normal).`},        
        {nom: "forcer-boutons-inline", type: "Boolean (Optionnel)", description: `Force l'affichage des boutons en mode inline (ne vont pas s'afficher en colonne  425px et moins).`},
        {nom: "affichage-lateral", type: "Boolean (Optionnel)", description: `Indique si une animation d'ouverture latrale (droite vers la gauche) est utilise pour l'affichage de la fentre modale.`}
    ];
}

function obtenirTableauSlots() {
    return [
        {nom: "dfaut", description: `<p>Slot par dfaut. Aucun nom  fournir.</p><p>Contenu html entre les balises du contrle. Est inject dans la zone de contenu de la fentre modale.</p>`},
        {nom: "pied", description: `Contenu html inject dans le pied de la fentre modale (gnralement des boutons).`}            
    ];
}

function ajouterCodeDialogue1() {
    document.getElementById('btnTest1').addEventListener('click', () => {
        utd.dialogue.afficher('dialogue1')
    })

    document.getElementById("btnAnnuler1").addEventListener('click', () => {
        //TODO, ici tout le code que vous devez effectuer sur click du bouton (gnralement pas grand chose sur une annulation)
        utd.dialogue.masquer('dialogue1')
    })

    document.getElementById("btnEnregistrer1").addEventListener('click', () => {
        //TODO, ici tout le code que vous devez effectuer sur click du bouton
        utd.dialogue.masquer('dialogue1')
    })
}

function ajouterCodeDialogue2() {
    document.getElementById('btnVotreAvis').addEventListener('click', () => {
        utd.dialogue.afficher('dialogueVotreAvis')
    })

    document.getElementById("btnAnnulerAvis").addEventListener('click', () => {
        //TODO, ici tout le code que vous devez effectuer sur click du bouton (gnralement pas grand chose sur une annulation)
        alert('Bravo le bouton "Annuler" a t cliqu. Vous pouvez mettre tout votre code ici!')
        utd.dialogue.masquer('dialogueVotreAvis')
    })

    document.getElementById("btnEnregistrerAvis").addEventListener('click', () => {
        alert('Bravo le bouton "Enregistrer" a t cliqu. Vous pouvez mettre tout votre code ici!')
        //TODO, ici tout le code que vous devez effectuer sur click du bouton
        utd.dialogue.masquer('dialogueVotreAvis')
    })
}

function ajouterCodeDialogue3() {
    document.getElementById('btnTest3').addEventListener('click', () => {
        utd.dialogue.afficher('dialogue3')
    })

    document.getElementById("btnAnnuler3").addEventListener('click', () => {
        //TODO, ici tout le code que vous devez effectuer sur click du bouton (gnralement pas grand chose sur une annulation)
        utd.dialogue.masquer('dialogue3')
    })

    document.getElementById("btnConfirmer3").addEventListener('click', () => {
        //TODO, ici tout le code que vous devez effectuer sur click du bouton
        utd.dialogue.masquer('dialogue3')
    })
}

function ajouterCodeDialogue4() {
    document.getElementById('btnTest4').addEventListener('click', () => {
        utd.dialogue.afficher('dialogue4')
    })

    document.getElementById("btnAnnuler4").addEventListener('click', () => {
        //TODO, ici tout le code que vous devez effectuer sur click du bouton (gnralement pas grand chose sur une annulation)
        utd.dialogue.masquer('dialogue4')
    })

    document.getElementById("btnConfirmer4").addEventListener('click', () => {
        //TODO, ici tout le code que vous devez effectuer sur click du bouton
        utd.dialogue.masquer('dialogue4')
    })
}

    /*
    document.getElementById("exempleDialogue1").addEventListener("fermeture", e => {
      console.log(`vnement de fermeture modale Test1 avec les dtails : ${e.detail.raisonFermeture}`)
    })
    */
</script>

<h1>Dialogue modal</h1>

<utd-menu-ancres selecteur="#main h2">
</utd-menu-ancres>

<h2>Description</h2>
<p>Le <strong>dialogue modal</strong> est une bote qui se superpose au contenu de la page, en plein centre,  la suite dune action dclenche directement par lutilisateur. Il sert  communiquer de linformation contextuelle.</p>
<p>Lorsque le dialogue modal est activ, larrire-plan de la page devient plus sombre pour quil soit en vidence.</p>

<h3>Rfrence systme de design Quebec.ca</h3>
<a href="https://design.quebec.ca/composantes/action/dialog-modal" target="_blank">Voir les spcifications sur le site de design Quebec.ca</a>

<h3>Particularits vs. le systme de design Quebec.ca</h3>
<p>Le composant <strong>dialogue d'alerte</strong> a t remplac par notre composant <strong><a href="/composants/actions/message">message</a></strong> (seulement le nom est diffrent, il a les mmes caractristiques).</p>

<h2>Attributs disponibles</h2>
<TableauParams parametres="{tableauParametres}">
</TableauParams>

<h2>Slots disponibles</h2>
<TableauSlots parametres="{tableauSlots}">
</TableauSlots>

<h2>Accessibilit</h2>
<p>Les <a href="https://www.w3.org/WAI/ARIA/apg/patterns/dialog-modal/examples/dialog" target="_blank">rgles du W3C relatives aux dialogues</a> sont suivies  la lettre.</p>
<p>Il semble que l'implmentation dans NVDA provoque la lecture du titre de la modale au lecteur cran deux fois.</p>
<p>Il s'agit malgr tout de la meilleure solution possible, et il semble qu'il n'y a malheureusement rien  faire.</p>

<h2>Exemples</h2>
<h3>1- Ouverture par le biais d'un bouton</h3>
<div id="exempleDialogue1">
    <utd-champ-form format="compact">
        <label>
            <input type="checkbox" name="grosContenu1" bind:checked={afficherGrosContenu} >
             <span>Afficher un long contenu dans la fentre modale</span>
        </label>               
    </utd-champ-form>

    <button type="button" id="btnTest1" class="utd-btn secondaire mb-32">Afficher</button>
    <utd-dialog id="dialogue1" titre="Ajout d'un problme">
        {#if afficherGrosContenu}
            <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit. Donec sit amet pulvinar ipsum. Donec luctus nulla scelerisque, congue nibh a, malesuada dolor. Sed mattis interdum pellentesque. Vivamus lacinia risus a justo imperdiet imperdiet at quis ligula. Cras a dictum tortor, sit amet venenatis tortor. Vestibulum aliquam elit eu venenatis laoreet. Aenean tellus enim, consectetur eu condimentum accumsan, malesuada volutpat lectus. Aliquam sit amet rhoncus mauris, a placerat mauris. Mauris neque nulla, vulputate a convallis in, imperdiet tincidunt leo.</p>        
            <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit. Donec sit amet pulvinar ipsum. Donec luctus nulla scelerisque, congue nibh a, malesuada dolor. Sed mattis interdum pellentesque. Vivamus lacinia risus a justo imperdiet imperdiet at quis ligula. Cras a dictum tortor, sit amet venenatis tortor. Vestibulum aliquam elit eu venenatis laoreet. Aenean tellus enim, consectetur eu condimentum accumsan, malesuada volutpat lectus. Aliquam sit amet rhoncus mauris, a placerat mauris. Mauris neque nulla, vulputate a convallis in, imperdiet tincidunt leo.</p>
        {/if}

        <label for="test1">Description du problme</label>
        <input type="text" id="test1" class="utd-form-group utd-form-control"/>

        <label for="test2">Solution</label>
        <input type="text" id="test2" class="utd-form-group utd-form-control"/>

        {#if afficherGrosContenu}
            <label for="test3">Solution 2</label>
            <input type="text" id="test3" class="utd-form-group utd-form-control"/>

            <label for="test4">Solution 3</label>
            <input type="text" id="test4" class="utd-form-group utd-form-control"/>

            <label for="test5">Solution 4</label>
            <input type="text" id="test5" class="utd-form-control"/>
        {/if}

        <div slot="pied">
            <button id="btnAnnuler1" type="button" class="utd-btn secondaire compact">Annuler</button>        
            <button id="btnEnregistrer1" type="button" class="utd-btn primaire compact">Enregistrer</button>        
        </div>
    </utd-dialog>
</div>
<CodeSource idElementCodeSource="exempleDialogue1" titre="Code source (Html)">
</CodeSource>   

{#if mounted}
    <CodeSource codeSource="{ajouterCodeDialogue1.toString()}" titre="Code source (js)" language="language-javascript">
