<h3>2- Ouverture latrale, affichage forc des boutons sur une ligne et focus  l'ouverture sur le contrle spcifi</h3>
<p>Utilisez le bouton "Votre avis"  droite fin de tester.</p>
<div id="exempleDialogueVotreAvis">
    <button type="button" id="btnVotreAvis" class="utd-btn primaire btn-avis">
        <span class="texte">Votre avis</span>
    </button>
    <utd-dialog id="dialogueVotreAvis" titre="Votre avis" affichage-lateral="true" forcer-boutons-inline="true" id-focus-ouverture="texteVotreAvis" sr-bouton-fermer="Exemple modification texte hors cran du bouton fermer.">
        <form>
            <div id="texteAvantAvis" class="utd-text-sm mb-32">
                <p>Pour toutes questions relatives  votre dossier, ou si vous avez besoin d'aide, n'hsitez pas  <a href="#NousJoindre">Nous joindre</a>.</p>
                <p>vitez d'inscrire des renseignements personnels. Notez que vous ne recevrez aucune rponse et que nous collectons votre avis dans le respect de votre anonymat.</p>
            </div>
            <textarea id="texteVotreAvis" class="utd-form-control" aria-describedby="erreurTexteVotreAvis" rows="3" placeholder="Parlez-nous de votre exprience..." required></textarea>
            <span id="erreurTexteVotreAvis" class="utd-erreur-champ utd-d-none" role="status" aria-live="polite">Le champ  Votre avis  est obligatoire.</span>
            <span id="texteApresAvis" class="utd-text-sm">
            </span>
        </form>
        <div slot="pied">
            <button id="btnAnnulerAvis" type="button" class="utd-btn secondaire compact">Annuler</button>        
            <button id="btnEnregistrerAvis" type="button" class="utd-btn primaire compact">Envoyer</button>        
        </div>
    </utd-dialog>
</div>
