<script>
    import CodeSource from '../components/CodeSource.svelte'; 
    import { onMount } from 'svelte';
    import TableauParams from '../components/TableauParams.svelte'; 
    import TableauSlots from '../components/TableauSlots.svelte'; 

    let tableauParametres = []
    let tableauSlots = []
    let tableauRetourEvenementChangementEtat = []
    let mounted = false

    onMount(() => {
        tableauParametres = obtenirTableauParametres()
        tableauSlots = obtenirTableauSlots()
        tableauRetourEvenementChangementEtat = obtenirTableauRetourEvenementChangementEtat()
        controlerAccordeon5()
        mounted = true
    })


    function obtenirTableauParametres() {
        return [
            {nom: "reduit", type: "Boolean (Optionnel)", description: `Indique si l'accordon doit tre rduit ou dvelopp. Peut tre mis  jour une fois le contrle gnr afin de modifier son tat d'affichage (dvelopp/rduit). Dfaut "true" (rduit).`},    
            {nom: "titre", type: "String (Optionnel)", description: `Titre affich dans l'entte de l'accordon.`},
            {nom: "tag-titre", type: "String (Optionnel)", description: `Balise html  utiliser pour le titre de l'entte. Dfaut "h2".`},
            {nom: "contenu", type: "String (Optionnel)", description: `Texte  afficher dans la zone de contenu de l'accordon.`},
            {nom: "conserver-etat-affichage", type: "Boolean (Optionnel)", description: `<p>Indique si l'tat d'affichage (dvelopp/rduit) de l'accordon doit tre conserv pendant la session (tant que le fureteur est ouvert). Dfaut "false".</p><p>IMPORTANT! Afin que la fonctionnalit de conservation d'tat d'affichage fonctionne, le composant utd-accordeon doit avoir un id.</p>`}
        ];
    }

    function obtenirTableauSlots() {
        return [
            {nom: "dfaut", description: `<p>Slot par dfaut. Aucun nom  fournir.</p><p>Contenu html entre les balises du contrle. Est inject dans la zone de contenu de l'accordon.</p>`},
            {nom: "titre", description: `Contenu html inject dans l'entte de l'accordon. Exemple d'utilisation : Ajout d'une icne  gauche du titre.`}            
        ];
    }

    function obtenirTableauRetourEvenementChangementEtat() {
        return [
            {nom: "reduit", type: "Boolean (Optionnel)", description: `Indique si l'accordon est rduit. Il s'agit du nouvel tat suite au changement d'tat d'affichage.`},    
        ];
    }

    function controlerAccordeon5() {        
        document.getElementById('btnControleExemple5').addEventListener('click', () => {            
            const accordeon = document.getElementById('utdAccordeon5')
            accordeon.setAttribute('reduit', accordeon.getAttribute('reduit') === 'false' ? 'true' : 'false')
        })

        document.getElementById("utdAccordeon5").addEventListener("changementEtat", e => {
            document.getElementById('resultat5').innerText = `tat d'affichage = ${e.detail.reduit ? 'rduit' : 'dvelopp'}`;
        })
    }

    //TODO faire une mthode gnrique et ajouter dans utils afin de patcher le problme d'ancres avec tinro
    function clickAncre(e) {
        e.preventDefault();
        location.hash = ''
        location.hash = e.target.getAttribute('href')
    }

</script>

<style type="text/css">
</style>

<div class="documentation">
    <h1>Accordon</h1>

    <utd-menu-ancres selecteur="#main h2">
    </utd-menu-ancres>

    <h2>Description</h2>
    <p>L<strong>accordon</strong> permet de rduire temporairement la quantit dinformation affiche sur une page. Avec cette option, la personne survole le contenu et accde plus rapidement  linformation recherche.</p>

    <h3>Rfrence systme de design Quebec.ca</h3>
    <a href="https://design.quebec.ca/composantes/affichage-de-contenu/accordeon" target="_blank">Voir les spcifications sur le site de design Quebec.ca</a>

    <h2>Attributs disponibles</h2>
    <TableauParams parametres="{tableauParametres}">
    </TableauParams>

    <h2>Slots disponibles</h2>
    <TableauSlots parametres="{tableauSlots}">
    </TableauSlots>

    <h2>vnements disponibles</h2>
    <h3>changementEtat</h3>
    <p>Est excut lorsque l'tat d'affichage (dvelopp/rduit) de l'accordon change. Voir exemple <a href="#aaa" on:click="{clickAncre}">5- Accordon contrl par javascript</a>.</p>
    <h4>Retour</h4>
    <TableauParams parametres="{tableauRetourEvenementChangementEtat}">
    </TableauParams>
</div>

<div class="backstop-test">
    <h2>Exemples</h2>
    <h3>1- Avec attributs "titre" et "contenu"</h3>

    <div class="mb-32 bs-test" id="exempleAccordeon1">
        <utd-accordeon titre="Exemple d'utilisation 1" contenu="<p>Ici un texte incroyable.</p><p>Qui peut mme contenir du html.</p>">
        </utd-accordeon>
    </div>
    <CodeSource idElementCodeSource="exempleAccordeon1">
    </CodeSource>   


    <h3>2- Avec attribut "titre", slot par dfaut et tag-titre h5</h3>
    <div class="mb-32" id="exempleAccordeon2">
        <utd-accordeon titre="Exemple d'utilisation 2" tag-titre="h5">
            <p>Dans cet exemple, le niveau de titre de l'accordon a t forc  "h5" au lieu de la valeur par dfaut "h2" via l'attribut <span class="utd-emphase-gris">tag-titre</span>.</p>
        </utd-accordeon>
    </div>
    <CodeSource idElementCodeSource="exempleAccordeon2">
    </CodeSource>   


    <h3>3- Avec slots "titre" et slot par dfaut et conservation de l'tat d'affichage</h3>
    <p>Dans cet exemple, l'tat d'affichage (dvelopp/rduit) et conserv tout au long de la session.</p> 
    <p>Pour tester, dvelopper l'accordon, changer de page et revenir dans cette page. Vous constaterez que l'accordon sera ouvert.</p>
    <div class="mb-32" id="exempleAccordeon3">
        <utd-accordeon class="mb-32" id="utdAccordeon3" conserver-etat-affichage="true">
            <span slot="titre">
                Exemple d'utilisation 3 avec un titre long long long long long long long long long long long long long long long long long long
            </span>   
            <p>Ici un texte incroyable.</p>
            <p>Qui peut mme contenir du html.</p>
        </utd-accordeon>
    </div>
    <CodeSource idElementCodeSource="exempleAccordeon3">
    </CodeSource>   


    <h3>4- Accordon dvelopp par dfaut</h3>

    <div class="mb-32 bs-test" id="exempleAccordeon4">
        <utd-accordeon class="mb-32" reduit="false">
            <span slot="titre">
                Exemple d'utilisation 4
            </span>   
            <p>Ici un texte incroyable.</p>
            <p>Qui peut mme contenir du html.</p>
        </utd-accordeon>
    </div>
    <CodeSource idElementCodeSource="exempleAccordeon4">
    </CodeSource>   

    <h3>5- Accordon contrl par javascript et conservation de l'tat d'affichage</h3>
    <p>Cet exemple dmontre l'utilisation de javascript afin de dvelopper/rduire l'accordon via un bouton.</p>
    <p>Il utilise galement l'vnement <span class="utd-emphase-gris">changementEtat</span> afin de signaler l'tat d'affichage de l'accordon (dvelopp/rduit) lorsque son tat change.</p>
    <p>Aussi dans cet exemple, l'tat d'affichage (dvelopp/rduit) et conserv tout au long de la session.</p> 
    <p>Pour tester, rduire l'accordon, changer de page et revenir dans cette page. Vous constaterez que l'accordon sera rduit.</p>

    <div class="mb-32 bs-test" id="exempleAccordeon5">
        <utd-accordeon class="mb-32" id="utdAccordeon5" reduit="false" conserver-etat-affichage="true">
            <span slot="titre">
                Exemple d'utilisation 5
            </span>   
            <p>Un bel exemple de comment contrler l'ouverture/fermeture de l'accordon via un bouton.</p>
            <p>Cliquez le bouton "Contrler accordon" pour essayer.</p>        
            <p>Remarquez galement le texte indiquant l'tat d'affichage lorsque ce dernier change.</p>
        </utd-accordeon>

        <button type="button" id="btnControleExemple5" class="utd-btn secondaire compact">Contrler accordon</button>
    </div>
    <div id="resultat5" class="mt-32 mb-32"></div>
    <CodeSource idElementCodeSource="exempleAccordeon5" titre="Code source (Html)">
    </CodeSource>   

    {#if mounted}
        <CodeSource codeSource="{controlerAccordeon5.toString()}" titre="Code source (js)" language="language-javascript">
        </CodeSource>   
    {/if}   
</div>