<script>
    import CodeSource from '../components/CodeSource.svelte'; 
    import { onMount } from 'svelte';
    import TableauParams from '../components/TableauParams.svelte'; 
    import TableauSlots from '../components/TableauSlots.svelte'; 

    let tableauParametres = [];
    let tableauSlots = [];

    onMount(() => {
        tableauParametres = obtenirTableauParametres();
        tableauSlots = obtenirTableauSlots();
    })

    function obtenirTableauParametres() {
        return [
            {nom: "type", type: "String (Optionnel)", description: `Type de l'avis (icne/couleur). Valeurs possibles : <span class="utd-emphase-gris">information</span>, <span class="utd-emphase-gris">avertissement</span>, <span class="utd-emphase-gris">succes</span>, <span class="utd-emphase-gris">erreur</span>, ou <span class="utd-emphase-gris">complementaire</span>. Dfaut "information".`},
            {nom: "titre", type: "String (Optionnel)", description: `Titre affich dans l'avis.`},
            {nom: "contenu", type: "String (Optionnel)", description: `Texte  afficher dans l'avis.`}
        ];
    }

    function obtenirTableauSlots() {
        return [
            {nom: "dfaut", description: `<p>Slot par dfaut. Aucun nom  fournir.</p><p>Contenu html entre les balises du contrle. Est inject dans la zone de contenu de l'avis.</p>`},
        ];
    }
</script>

<style type="text/css">
</style>

