    <a href="https://design.quebec.ca/composantes/affichage-de-contenu/avis" target="_blank">Voir les spcifications sur le site de design Quebec.ca</a>

    <h2>Attributs disponibles</h2>
    <TableauParams parametres="{tableauParametres}">
    </TableauParams>

    <h2>Slots disponibles</h2>
    <TableauSlots parametres="{tableauSlots}">
    </TableauSlots>
</div>

