<h2>Description</h2>
<p>La composante est constitue de deux lments : la mention et l'astrisque.</p>
<p>La mention saffiche sous le titre du formulaire avec la phrase :  Les champs suivis d'un * sont obligatoires . Elle doit toujours tre prsente ds que la tche dpasse 2 champs de formulaire.</p>
<p>Lastrisque est plac  la fin de ltiquette de chacun des champs obligatoires. Il est toujours en rouge pour que l'utilisateur puisse reprer rapidement quel champ est obligatoire.
   Il suffit d'utiliser un &lt;span&gt; avec la classe <span class="utd-emphase-gris">utd-icone-champ-requis</span> ayant comme contenu une toile "*".
</p>
<p>Lattribut <span class="utd-emphase-gris">aria-required</span> doit tre spcifi avec la valeur "true" afin d'indiquer au lecteur cran que le champ est obligatoire.</p>
<p>Il serait possible d'utiliser l'attribut HTML5 <span class="utd-emphase-gris">required</span>, cependant ds qu'on accde  un champ <em>required</em>, le lecteur cran indique que le champ est invalide s'il est vide, et ce mme si le champ n'a pas encore t touch.</p>

<h2>Exemples</h2>
<h3>Exemple 1</h3>
<div id="exempleChampObligatoire">
    <div class="utd-form-group">
        <label for="tel1">Tlphone<span class="utd-icone-champ-requis" aria-hidden="true">*</span></label>
        <span id="tel1Precision" class="utd-precision">Si vous navez pas de numro de tlphone, inscrivez le numro dun membre de votre famille, dun ami ou dun organisme.</span>
        <input type="tel" id="tel1" placeholder="123 456-7890" aria-required="true" aria-describedby="tel1Precision" class="utd-form-control md"/>
    </div>
</div>
