    <h2>Description</h2>
    <p>Le <strong>champ texte</strong> est un espace dans lequel est saisie une information qui tient sur une ligne de texte. </p>
    <p>Le champ est accompagn dune tiquette claire et succincte. Elle permet didentifier au premier coup dil linformation requise dans le champ. Les signes de ponctuation sont  viter.</p>
    <p>On vite aussi dajouter des complments dinformation entre les champs dun formulaire. Les prcisions sont fournies  laide :</p>
    <ul>
        <li>dune tiquette claire et succincte;</li>
        <li>dun espace rserv (<em>placeholder</em>), au besoin;</li>
        <li>dune courte phrase sous ltiquette, au besoin;</li>
    </ul>

    <h3>Rfrence systme de design Quebec.ca</h3>
    <a href="https://design.quebec.ca/composantes/formulaire/champ-texte" target="_blank">Voir les spcifications sur le site de design Quebec.ca</a>


    <h2>Notions de base</h2>
    <p>Un champ de type &lt;input&gt; doit possder la classe <span class="utd-emphase-gris">utd-form-control</span> et tre dans un conteneur (ex. un &lt;div&gt;) ayant la classe <span class="utd-emphase-gris">utd-form-group</span> (sauf le dernier champ d'une page).</p>


    <h2>tiquette (label)</h2>
    <p>Style appliqu automatiquement  tous les lments &lt;label&gt;.</p>

    <h3>Particularits vs. le systme de design Quebec.ca</h3>
    <p>Nous utilisons la graisse semi-bold au lieu de bold.</p>

    <h3>Exemples</h3>
    <div id="exempleLabel">
        <div class="utd-form-group">
            <label for="label1">Un beau libell de champ</label>
            <input type="text" id="label1" class="utd-form-control"/>
        </div>
    </div>

    <CodeSource idElementCodeSource="exempleLabel">
    </CodeSource>

    <h2 id="formatsDisponibles">Formats disponibles</h2>
    <p>La longueur du champ doit tre adapte  la rponse requise, selon 5 formats disponibles.</p>
    <p>Utilisez les classes <span class="utd-emphase-gris">sm</span>, <span class="utd-emphase-gris">md</span>, <span class="utd-emphase-gris">lg</span>, <span class="utd-emphase-gris">xl</span>, et <span class="utd-emphase-gris">xxl</span> afin de prciser le format des champs. Si rien n'est spcifi, le format "large" par dfaut s'applique.</p>

    <h3>Particularits vs. le systme de design Quebec.ca</h3>
    <p>Nous avons ajout un format "extra extra-large" <span class="utd-emphase-gris">xxl</span> prenant la largeur disponible  l'intrieur du conteneur.</p>
</div>

<div id="exemples">
    <h3>Exemples</h3>
    <div id="exempleFormats">
