        </div>
        <div class="utd-form-group">
            <label for="taille3">Champ "large" (<em>lg</em>)</label>
            <input type="text" id="taille3" class="utd-form-control lg"/>
        </div>
        <div class="utd-form-group">
            <label for="taille4">Champ "extra-large" (<em>xl</em>)</label>
