    <h2>Espace rserv et prcision</h2>
    <p>Un espace rserv (<em>placeholder</em>) permet de prciser comment saisir les informations. Il est utilis dans les champs dont le format est particulier, comme le code postal, le numro de tlphone ou la date.</p>
    <p>La prcision quant  elle permet d'ajouter certaines informations permettant de mieux comprendre ce qui doit tre inscrit dans un champ. Elle sajoute  la suite de ltiquette, avant le champ texte. Simplement utiliser un &lt;span&gt; avec la classe <span class="utd-emphase-gris">utd-precision</span>.</p>
    <p>Lors de l'utilisation d'une prcision, il est impratif d'ajouter l'attribut <span class="utd-emphase-gris">aria-describedby</span> sur le champ afin que la prcision soit lue au lecteur cran avec le libell.</p>
    <h3>Exemples</h3>
    <div id="exemplePlaceholderPrecision">
