<h1>Rgles d'interfaces</h1>

<p>Tous les sites Web dvelopps au sein du ministre de l'Emploi et de la Solidarit sociale doivent tre conformes  ces standards du gouvernement :</p>


<utd-accordeon titre="Programme d'identification visuelle (PIV)" tag-titre="h2">

    <p>Toutes les communications du gouvernement, des ministres et des organismes gouvernementaux sont soumises aux normes graphiques du PIV : <a href="https://www.piv.gouv.qc.ca" target="_blank">www.piv.gouv.qc.ca</a></p>

    <h3>PIV standard</h3>
    <p>Tous les sites Web gouvernementaux doivent tre identifis  l'aide d'un bandeau d'en-tte et/ou d'un pied de page, qu'ils servent  des fins informationnelles, transactionnelles ou promotionnelles et qu'ils soient destins au grand public (site Internet),  une clientle interne (intranet) ou  une clientle externe spcifique (extranet).</p>
    <p>Voir les composants <a href="/composants/entetepiedpage/piventete">entte</a> et <a href="/composants/entetepiedpage/pivpiedpage">pied de page</a> pour les bandeaux PIV.</p>        

    <p>Pour les informations relatives aux lments qui doivent tre prsents dans l'entte et le pied de page, se rfrer  :</p>
    <ul class="utd">
        <li>Section 4.4 du <a href="https://www.piv.gouv.qc.ca/fileadmin/documents/guide/section4_numerique.pdf" target="_blank">Guide des normes graphiques du Programme d'identification visuelle</a> (.PDF);</li>
        <li><a href="https://www.piv.gouv.qc.ca/fileadmin/documents/guide/infos_complementaires.pdf" target="blank">Guide des bonnes pratiques et informations complmentaires au Programme d'identification visuelle</a> (.PDF) (Nouvelle dition, mai 2022).</li>
    </ul>
        
            
    <h3>PIV appliqu aux progiciels et applications</h3>
    <p>Certains sites sont des applications en ligne conues pour les besoins propres  un groupe cibl. Lorsque la navigation du site et la mise en page sont intgres dans une solution logicielle cls en mains, il faut <strong>appliquer le bandeau d'en-tte et le pied de page dans la mesure de ce qui est possible</strong>. Dans les cas o il faut retrancher certains lments, le positionnement et la grosseur des signatures ne doivent pas tre modifis.</p>
    <p>Les normes pour les progiciels en ligne qui s'affichent via le navigateur Web se basent gnralement sur les normes des sites Web. On peut aussi les adapter, en fonction des limites que l'outil impose aux interfaces.</p>
    <p>Si possible, apposer le bandeau contenant le logo Qubec et y afficher le nom du site (ou du service qui utilise le progiciel). La zone personnalisable peut rester vide.</p>
    <p>S'il n'est pas possible d'apposer le bandeau, afficher le logo Qubec en haut,  gauche, dans toutes les pages du progiciel.</p>
</utd-accordeon>
        
<utd-accordeon titre="Systme de design gouvernemental" tag-titre="h2">
    <p>Le <a href="https://design.quebec.ca/apercu/systeme" target="_blank">Systme de design gouvernemental</a> est le rfrentiel UX et UI qui s'adresse aux quipes (TI et communications) des ministres et organismes, ainsi qu'aux collaborateurs amens  travailler avec le gouvernement sur des projets numriques. L'objectif premier est d'assurer la cohrence des interfaces gouvernementales (information et services) afin d'offrir aux citoyennes et citoyens une exprience simplifie et uniforme.</p>
    <p>Le systme de design regroupe la charte ergonomique et la charte graphique appliques  un site Web ou une application. Il prsente les principes et lignes directrices, les guides de style (couleurs, typographie), les patterns et grilles pour structurer les lments, une bibliothque de composants d'interface, le langage visuel (iconographie) et de l'information sur le contenu : principes de rdaction web, langage  adopter, structure du contenu, typographie (utilisation du gras, soulignement, italique), libells, rfrencement SEO, etc.</p>
    <p><strong>L'application stricte du systme de design est obligatoire</strong>. Vous devez utiliser la charte graphique et toutes les composantes et rgles d'interface tels que dcrites dans ce systme.</p>
    <p><strong>Tous nos composants web sont bass sur ce systme de design</strong>, et sont accessibles et "responsive".</p>

    <h3>volution et collaboration</h3>
    <p>Le systme de design est volutif. Lorsqu'une nouvelle interface est dveloppe, les besoins de styles et de composantes sont identifis  l'tape des maquettes schmatiques. Si les styles et composantes ncessaires  cette interface se trouvent dans le systme de design, ils doivent tre utiliss. Si les styles ou composantes ne rpondent pas tout  fait au besoin, ou n'existent tout simplement pas, les nouvelles composantes doivent tre dveloppes par l'quipe UX en collaboration avec la Direction des communications numriques gouvernementales (DCNG).</p>    
</utd-accordeon>

<utd-accordeon titre="Standard sur l'accessibilit des sites Web" tag-titre="h2" class="mb-32">

    <p>Un contenu ou un service offert sur le Web est considr comme accessible lorsque toute personne, peu importe ses incapacits, peut le comprendre, y naviguer et interagir avec lui. L'accessibilit Web reprsente un lment important pour permettre l'intgration sociale et la participation  la vie collective de la population.</p>    
    <p>Une attention particulire doit tre porte lors du dveloppement des interfaces pour y intgrer le Standard du gouvernement du Qubec en ressources informationnelles (SGQRI) : <a href="https://www.tresor.gouv.qc.ca/fileadmin/PDF/ressources_informationnelles/AccessibiliteWeb/standard-access-web.pdf" target="_blank">Standard sur l'accessibilit des sites Web (SGQRI 008 2.0)</a> (.PDF).</p>
    <p>Depuis sa modernisation, en 2018, ce Standard s'appuie sur les <a href="https://www.w3.org/Translations/WCAG20-fr/">Rgles pour l'accessibilit des contenus Web 2.0 (WCAG 2.0)</a> recommandes par le World Wide Web Consortium (W3C), qui sont devenues une norme internationale (ISO/IEC 40500:2012). Tout contenu Web doit satisfaire les exigences de conformit prvues dans les WCAG 2.0.</p>    
    <p>Les composantes dveloppes dans le systme de design et les couleurs qui y sont recommandes respectent les principaux standards d'accessibilit.</p>
          
    <p>Pour plus d'information, consultez les pages traitant d'accessibilit :</p>
    <ul>
        <li>dans notre intranet : <a href="https://intranet-mtess.mes.reseau.intra/ressources-services/communications/accessibilite-web/">Accessibilit Web</a>;</li>
        <li>sur le site du SCT : <a href="https://www.tresor.gouv.qc.ca/ressources-informationnelles/architecture-dentreprise-gouvernementale/standards-et-normes/accessibilite-du-web/" target="_blank">Les standards d'accessibilit du gouvernement du Qubec</a>.</li>
    </ul>
    
    <p></p>    
</utd-accordeon>

<p><strong>Ces rgles s'appliquent  tous les sites Web</strong>. Pour les sites Web particuliers (intranet, progiciels, sondage, bulletin Web, etc.), ces rgles sont aussi une rfrence, mais ils pourront tre traits au cas par cas puisque les rgles d'affichage peuvent varier selon le type de plateforme de dveloppement, de service Web et de clientle.</p>
<p><strong>Tout nouveau site Web qui sera mis en ligne devra respecter ces rgles d'interface Web, le PIV et le systme de design gouvernemental</strong>. Les sites mis en ligne dans le pass devront appliquer ces rgles lors des dveloppements en continuit ou lors de demandes d'amlioration.</p>

