<script>
  import { onMount } from 'svelte';
  import CodeSource from '../components/CodeSource.svelte'; 
  import TableauParams from '../components/TableauParams.svelte'; 
  import TableauSlots from '../components/TableauSlots.svelte'; 

  let tableauParametres = [];
  let tableauParametres2 = [];
  let tableauSlots = [];

  onMount(() => {
      tableauParametres = obtenirTableauParametres();
      tableauParametres2 = obtenirTableauParametres2();
      tableauSlots = obtenirTableauSlots();
  })

  function obtenirTableauParametres() {
      return [
          {nom: "titre", type: "String (Optionnel)", description: `Titre lu au lecteur cran seulement. Dfaut : "Menu principal de navigation" / "Main navigation menu".`},
          {nom: "largeur-viewport-menu-burger", type: "Integer (Optionnel)", description: `Largeur d'cran  partir de laquelle l'affichage du menu est forc en mobile, i.e Affichage d'un seul lment de menu (menu burger) nomm "Menu". Dfaut : 475.`},          
          {nom: "afficher-icone-accueil", type: "Boolen (Optionnel)", description: `Indique si le lien "accueil/home" (icne "maison") doit tre affich ou non. Dfaut : false.`},          
          {nom: "titre-accueil", type: "String (Optionnel)", description: `Title (icne maison) et texte (dans le menu burger) associ au lien "accueil" si affich. Sera aussi le texte affich  Dfaut : Accueil / Home.`},          
          {nom: "url-accueil", type: "String (Optionnel)", description: `Url vers laquelle le lien "accueil" doit rediriger. Dfaut : "/".`}
      ];
  }

  function obtenirTableauParametres2() {
      return [
          {nom: "libelle", type: "String", description: `Texte du lien associ  l'item de menu. Il est possible d'ajouter "&lt;br/&gt;" afin de forcer un saut de ligne dans le texte.`},
          {nom: "href", type: "String (Optionnel)", description: `Valeur de l'attribut "href" du lien dans l'item de menu.`},
      ];
  }

  function obtenirTableauSlots() {
      return [
        {nom: "dfaut", description: `<p>Slot par dfaut. Aucun nom  fournir.</p><p>Contenu html entre les balises du contrle. Ne devrait contenir que des composants <span class="utd-emphase-gris">utd-menu-horizontal-item</span>.</p>`}
      ];
  }


</script>

<h1>Menu horizontal</h1>

<utd-menu-ancres selecteur="#main h2">
</utd-menu-ancres>

<h2>Description</h2>
<p>Menu de navigation horizontal applicatif  deux niveaux. Il est gnr  partir de deux composants, soit <span class="utd-emphase-gris">utd-menu-horizontal</span> et <span class="utd-emphase-gris">utd-menu-horizontal-item</span>.</p>
<p>Le composant <span class="utd-emphase-gris">utd-menu-horizontal</span> contient l'ensemble du menu.</p>
<p>Le composant <span class="utd-emphase-gris">utd-menu-horizontal-item</span> correspond  un lment du menu et peut contenir d'autres composants <span class="utd-emphase-gris">utd-menu-horizontal-item</span> (menu/sous-menu).</p>
<p>Comme il s'agit d'un menu horizontal "applicatif" et non "informationnel", seuls deux niveaux sont supports.</p>

<h3>Rfrence systme de design Quebec.ca</h3>
<p>Non disponible.</p>

<h3>Rfrences d'accessibilit</h3>
<p>Le "disclosure pattern" <a href="https://www.w3.org/WAI/ARIA/apg/patterns/disclosure/examples/disclosure-navigation/" target="_blank">tel que dfini au W3C</a> a t utilis.</p>

<h3>Particularits</h3>
<p>Le menu horizontal faire partie du "bandeau principal" qui est constitu du menu principal (le prsent) et d'une zone de raccourcis et d'lments lis  la connexion.</p>
<p>La zone de raccourcis et d'lments lis  la connexion/dconnexion ne fait pas partie du menu horizontal, et son contenu devrait se restreindre  quelques icnes de raccourcis et un bouton de connexion/dconnexion.</p>
<p>Cependant les deux doivent tre intgrs dans une structure html particulire. Voir les lments de classe <span class="utd-emphase-gris">utd-bandeau-principal</span>, <span class="utd-emphase-gris">utd-container</span> et <span class="utd-emphase-gris">utd-zone-raccourcis-connexion</span> dans l'<a href="#exemple">exemple</a>.</p>

<h2>utd-menu-horizontal - Attributs disponibles</h2>
<TableauParams parametres="{tableauParametres}">
</TableauParams>

<h2>utd-menu-horizontal-item - Attributs disponibles</h2>
<TableauParams parametres="{tableauParametres2}">
</TableauParams>

<h2>Slots disponibles (utd-menu-horizontal et utd-menu-horizontal-item)</h2>
<TableauSlots parametres="{tableauSlots}">
</TableauSlots>



<h2 id="exemple">Exemple</h2>
<p>L'exemple contient le menu horizontal du prsent site de dmonstration avec quelques lments fictifs supplmentaires visibles uniquement dans la prsente page.</p>

<utd-avis titre="Important!">
    <p>Le menu horizontal doit tre intgr dans une structure html particulire. Voir les lments de classe <span class="utd-emphase-gris">utd-bandeau-principal</span> et <span class="utd-emphase-gris">utd-container</span> dans l'exemple.</p>
</utd-avis>
<CodeSource idElementCodeSource="bandeauPrincipal" outerhtml="true">
</CodeSource>


