<script>
    import CodeSource from '../components/CodeSource.svelte'; 
    import { onMount } from 'svelte';
    import TableauParams from '../components/TableauParams.svelte'; 
    import TableauSlots from '../components/TableauSlots.svelte'; 

    let tableauParametres = [];
    let tableauSlots = [];
    let tableauRetourEvenementChangementEtat = [];
    let mounted = false;

    onMount(() => {
        tableauParametres = obtenirTableauParametres()
        tableauSlots = obtenirTableauSlots()
        tableauRetourEvenementChangementEtat = obtenirTableauRetourEvenementChangementEtat()
        controlerSection7()
        mounted = true
    })


    function obtenirTableauParametres() {
        return [
            {nom: "extensible", type: "Boolean (Optionnel)", description: `Indique si la section doit tre extensible ou non. Dfaut "true" (extensible).`},        
            {nom: "reduit", type: "Boolean (Optionnel)", description: `Indique si la section doit tre rduite ou dveloppe. Peut tre mis  jour une fois le contrle gnr afin de modifier son tat d'affichage (dveloppe/rduite). Dfaut "true" (rduite).`},    
            {nom: "bordure", type: "Boolean (Optionnel)", description: `Indique si la section doit tre affiche avec bordure ou non. Dfaut "true" (avec bordure).`},    
            {nom: "titre", type: "String (Optionnel)", description: `Titre affich dans l'entte de la section.`},
            {nom: "tag-titre", type: "String (Optionnel)", description: `Balise html  utiliser pour le titre de l'entte. Dfaut "h2".`},
            {nom: "conserver-etat-affichage", type: "Boolean (Optionnel)", description: `<p>Indique si l'tat d'affichage (dvelopp/rduit) de la section doit tre conserv pendant la session (tant que le fureteur est ouvert). Dfaut "false".</p><p>IMPORTANT! Afin que la fonctionnalit de conservation d'tat d'affichage fonctionne, le composant utd-section doit avoir un id.</p>`}
        ];
    }

    function obtenirTableauSlots() {
        return [
            {nom: "dfaut", description: `<p>Slot par dfaut. Aucun nom  fournir.</p><p>Contenu html entre les balises du contrle. Est inject dans la zone de contenu de la section.</p>`},
            {nom: "titre", description: `Contenu html inject dans l'entte de la section. Exemple d'utilisation : Ajout d'une icne  gauche du titre.`}            
        ];
    }

    function obtenirTableauRetourEvenementChangementEtat() {
        return [
            {nom: "reduit", type: "Boolean (Optionnel)", description: `Indique si la section est rduite. Il s'agit du nouvel tat suite au changement d'tat d'affichage.`},    
        ];
    }

    function controlerSection7() {
        document.getElementById('btnControleExemple7').addEventListener('click', () => {
            
            const section = document.getElementById('utdSection7')
            section.setAttribute('reduit', section.getAttribute('reduit') === 'false' ? 'true' : 'false')
        })

        document.getElementById("utdSection7").addEventListener("changementEtat", e => {
            document.getElementById('resultat7').innerText = `tat d'affichage = ${e.detail.reduit ? 'rduit' : 'dvelopp'}`;
        })
    }

    //TODO faire une mthode gnrique et ajouter dans utils afin de patcher le problme d'ancres avec tinro
    function clickAncre(e) {
        e.preventDefault();
        location.hash = ''
        location.hash = e.target.getAttribute('href')
    }


    

</script>

<style type="text/css">
</style>

<div class="documentation">
    <h1>Section</h1>

    <utd-menu-ancres selecteur="#main h2">
    </utd-menu-ancres>

    <h2>Description</h2>
    <p>Le composant <strong>section</strong> permet d'organiser le contenu en regroupant les informations en sections principales ou en blocs d'informations.</p>
    <p>Selon les besoins, cette section peut tre extensible ou non, et avoir une bordure ou non pour regrouper son contenu.</p>

    <h3>Rfrence systme de design Quebec.ca</h3>
    N'existe pas sur le site de design Quebec.ca

    <h2>Attributs disponibles</h2>
    <TableauParams parametres="{tableauParametres}">
    </TableauParams>

    <h2>Slots disponibles</h2>
    <TableauSlots parametres="{tableauSlots}">
    </TableauSlots>

    <h2>vnements disponibles</h2>
    <h3>changementEtat</h3>
    <p>Est excut lorsque l'tat d'affichage (dvelopp/rduit) de la section change. Voir exemple <a href="#exempleSectionJs" on:click="{clickAncre}">7- Section contrle par javascript</a>.</p>
    <h4>Retour</h4>
    <TableauParams parametres="{tableauRetourEvenementChangementEtat}">
    </TableauParams>
</div>
<h2>Exemples</h2>
<h3>1- Section avec champs</h3>
<div class="mb-32 bs-test" id="exempleSection1">
    <utd-section titre="Vhicule principal">
        <div class="utd-form-group">
            <label for="champ1a">Marque</label>
            <input type="text" id="champ1a" class="utd-form-control"/>
        </div>
    
        <div class="utd-form-group">
            <label for="champ2a">Modle</label>
            <input type="text" id="champ2a" class="utd-form-control"/>
        </div>
    
        <div class="utd-form-group">
            <label for="champ3a">Numro de srie</label>
            <input type="text" id="champ3a" class="utd-form-control"/>
        </div>
    </utd-section>
</div>
<CodeSource idElementCodeSource="exempleSection1">
</CodeSource>   


<h3>2- Section avec accordons et conservation de l'tat d'affichage</h3>
<div class="mb-32 bs-test" id="exempleSection2">
    <utd-section titre="Vhicules" id="utdSection2" conserver-etat-affichage="true">
        <utd-accordeon titre="Vhicule principal" id="utdSection2Acc1" conserver-etat-affichage="true">
            <div class="utd-form-group">
                <label for="champ1b">Marque</label>
                <input type="text" id="champ1b" class="utd-form-control"/>
            </div>
        
            <div class="utd-form-group">
                <label for="champ2b">Modle</label>
                <input type="text" id="champ2b" class="utd-form-control"/>
            </div>
        
            <div class="utd-form-group">
                <label for="champ3b">Numro de srie</label>
                <input type="text" id="champ3b" class="utd-form-control"/>
            </div>
        </utd-accordeon>
        <utd-accordeon titre="Vhicule secondaire" id="utdSection2Acc2" conserver-etat-affichage="true">
            <div class="utd-form-group">
                <label for="champ4b">Marque</label>
                <input type="text" id="champ4b" class="utd-form-control"/>
            </div>
        
            <div class="utd-form-group">
                <label for="champ5b">Modle</label>
                <input type="text" id="champ5b" class="utd-form-control"/>
            </div>
        
            <div class="utd-form-group">
                <label for="champ5b">Numro de srie</label>
                <input type="text" id="champ5b" class="utd-form-control"/>
            </div>
        </utd-accordeon>
    </utd-section>
</div>
<CodeSource idElementCodeSource="exempleSection2">
</CodeSource>   

<h3>3- Section sans bordure</h3>
<div class="mb-32 bs-test" id="exempleSection3">
    <utd-section titre="Vhicules"  bordure="false">
        <div class="utd-form-group">
            <label for="champ1c">Marque</label>
            <input type="text" id="champ1c" class="utd-form-control"/>
        </div>
    
        <div class="utd-form-group">
            <label for="champ2c">Modle</label>
            <input type="text" id="champ2c" class="utd-form-control"/>
        </div>
    
        <div class="utd-form-group">
            <label for="champ3c">Numro de srie</label>
            <input type="text" id="champ3c" class="utd-form-control"/>
        </div>
    </utd-section>
</div>
<CodeSource idElementCodeSource="exempleSection3">
</CodeSource>   


<h3>4- Section non extensible</h3>
<div class="mb-32 bs-test" id="exempleSection4">
    <utd-section titre="Informations personnelles" extensible="false">
        <p>Le mot de passe est associ  votre compte clicSQUR - Citoyens.</p>
        <p>
            <a href="#">Modifiez votre mot de passe</a>
        </p>
    </utd-section>
</div>

<CodeSource idElementCodeSource="exempleSection4">
</CodeSource>   

<h3>5- Section non extensible sans bordure</h3>
<div class="mb-32 bs-test" id="exempleSection5">
    <utd-section titre="Informations personnelles" extensible="false" bordure="false">
        <p>Le mot de passe est associ  votre compte clicSQUR - Citoyens.</p>
        <p>
            <a href="#">Modifiez votre mot de passe</a>
        </p>
    </utd-section>
</div>
<CodeSource idElementCodeSource="exempleSection5">
</CodeSource>   

<h3>6- Section ouverte par dfaut et titre h3</h3>
<div class="mb-32 bs-test" id="exempleSection6">
    <utd-section titre="Informations personnelles" reduit="false" tag-titre="h3">
        <p>Le mot de passe est associ  votre compte clicSQUR - Citoyens.</p>
        <p>
            <a href="#">Modifiez votre mot de passe</a>
        </p>
    </utd-section>
</div>
<CodeSource idElementCodeSource="exempleSection6">
</CodeSource>   


<h3 id="exempleSectionJs">7- Section contrle par javascript et conservation de l'tat d'affichage</h3>
<div class="mb-32 bs-test" id="exempleSection7">
    <utd-section class="mb-32" id="utdSection7" reduit="false" conserver-etat-affichage="true">
        <span slot="titre">
            Exemple d'utilisation 7
        </span>   
        <p>Un bel exemple de comment contrler l'ouverture/fermeture de la section via un bouton.</p>
        <p>Cliquez le bouton "Contrler section" pour essayer.</p>
        <p>Remarquez galement le texte indiquant l'tat d'affichage lorsque ce dernier change.</p>
    </utd-section>

    <button type="button" id="btnControleExemple7" class="utd-btn secondaire compact">Contrler section</button>
</div>
<div id="resultat7" class="mt-32 mb-32"></div>
<CodeSource idElementCodeSource="exempleSection7" titre="Code source (Html)">
</CodeSource>   

{#if mounted}
    <CodeSource codeSource="{controlerSection7.toString()}" titre="Code source (js)" language="language-javascript">
    </CodeSource>   
{/if}   

